input, button {
    padding: nice-rem(.35) nice-rem(.65);
    border: 1px solid map-get($colors, border);
}

[type="submit"], button {
    &:not(:disabled) {
        cursor: pointer;
    }
}

button {
    display: inline-block;
    background-color: map-get($colors, btn-bg);
    border-radius: $basic-border-radius;

    &:hover {
        background-color: darken(map-get($colors, btn-bg), 5%);
    }
}

.input {
    border-radius: $basic-border-radius;
}

.input-box {
    display: inline-flex;

    .input-box-prev {
        flex: 0 0 auto;
        padding: nice-rem(.35) nice-rem(.65);
        border: 1px solid map-get($colors, border);
        border-right-width: 0;
        background-color: map-get($colors, btn-bg);
        border-top-left-radius: $basic-border-radius;
        border-bottom-left-radius: $basic-border-radius;
    }

    .input {
        border-top-left-radius: 0;
        border-bottom-left-radius: 0;
    }
}
